import React, { Component } from 'react'
import {Route,Link} from 'react-router-dom'
import './content.css'
export default class Content extends Component {
    constructor(){
        super();
        this.state={
            data:[],
            ID:''
        }
        console.log("2222");
        // this.history=useHistory();
        // console.log(this.history);
    }
    // componentDidMount(){
    //     console.log("发的得分率时间外婆温柔的抚摩");
    // }
    componentDidMount(){
        let page=this.props.match.params;
        let ID=this.props.location.pathname.slice(9);
        console.log(this.props.location.pathname.slice(10));
        fetch('https://cnodejs.org/api/v1/topics?page='+page)
        .then((res)=>res.json())
        .then((res)=>{
            console.log("res:",res);
            this.setState({
                data:res.data,
                ID:ID
            })
        })
    }
    componentDidUpdate(prevProps,prevState){
        if(prevProps.match.params.id!==this.props.match.params.id){
            let page=this.props.match.params.id;
            fetch('https://cnodejs.org/api/v1/topics?page='+page)
            .then((res)=>res.json())
            .then((res)=>{
                console.log(res);
                this.setState({
                    data:res.data
                })
            })
        }
    }
    render() {
        return (
            
            <div>{
                this.state.data.map((item)=>{
                    if(this.state.ID===item.id){
                    return(
                    <div style={{background:"white",width:800,float:"left"}}
                    dangerouslySetInnerHTML={{__html:item.content}}></div>
                )
                    } 
                 } )
            }
            {/* <div style={{float:"right",width:100,height:500,background:"red"}}></div> */}
            </div>
            // <div style={{background:"red",width:200,height:200}}>
            //     11111
            // </div>
        )
    }
}
