import React, { Component } from 'react'
import './login.css'

export default function Login(props) {
    function toF(){
        props.history.push('/home')
    }
         
        return (
            <div className='login'>
                用户名<input className="name" type='text' name="name"/>
                <br/>
                密码<input className='pwd' type='password' name="pwd"/>
                <br/>
                <button onClick={toF}>登录</button>
            </div>
        )
}
